    --softBlack: rgba(0,0,0,0.75);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    color: white;
    display: grid;
    grid-template-rows: 200px 1fr 250px;
    background-color: black;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    font-size: 1.2em;
}

/* Classes */

.button {
    cursor: pointer;
}

.icon {
    filter: invert(100%) sepia(0%) saturate(18%) hue-rotate(314deg) brightness(106%) contrast(104%);
}

.scaled {
    scale: 2.7;
}

.title-font {
    font-family: 'Urbanist', sans-serif;
    font-weight: 300;
    font-size: 1.7em;
}

/* Paragraphs */

.txt-wrapper {
    padding: 30px;
    background-color: var(--softBlack);
}

/* Images */

.img-wrapper:first-of-type {
    float: right;
    /* padding-right: 40px; */
}

.img-wrapper:last-of-type {
    float: left;
    padding-right: 10px;
}

/* #HEADER */

#header, #footer {
    background-color: black;
}

#header {
    display: inline-grid;
    grid-template-columns: 1fr 2fr 1fr;
    padding: 20px 0;
    align-content: center;
}

#home {
    position: relative;
    /* display: grid; */
}

#home p {
    margin-left: 60px;
    scale: 1.2;
    position: absolute;
    top: 5px;
    border: 1px solid white;
}

#home .button {
    scale: 1.7;
}

#title {
    justify-self: center;
}

#navbar {
    display: flex;
    justify-content: flex-end;
}

#navbar :last-child {
    margin-left: 30px;
    margin-right: 60px;
}

/* #CONTENT */

#content {
    display: grid;
    justify-items: center;
}

#modules {
    display: grid;
    gap: 40px;
    justify-items: center;
    width: 80%;
    /* height: 80%; */
    background-color: rgba(255, 217, 0, 0.2);
    padding: 50px;
    margin: 100px;
}

#modules p { 
    margin-bottom: 10px;
    font-family: 'Noto Serif', serif;
} 

/* #FOOTER */

#footer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
}

#socials, #address {
    align-self: center;
}

#socials p {
    display: flex;
    gap: 40px;
}

#map {
    margin: 15px;
    width: 60%;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,6BAA6B;AACjC;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,aAAa;IACb,mCAAmC;IACnC,uBAAuB;IACvB,yDAAqD;IACrD,4BAA4B;IAC5B,gBAAgB;AACpB;;AAEA,YAAY;;AAEZ;IACI,eAAe;AACnB;;AAEA;IACI,+FAA+F;AACnG;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,mCAAmC;IACnC,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA,eAAe;;AAEf;IACI,aAAa;IACb,kCAAkC;AACtC;;AAEA,WAAW;;AAEX;IACI,YAAY;IACZ,yBAAyB;AAC7B;;AAEA;IACI,WAAW;IACX,mBAAmB;AACvB;;AAEA,YAAY;;AAEZ;IACI,uBAAuB;AAC3B;;AAEA;IACI,oBAAoB;IACpB,kCAAkC;IAClC,eAAe;IACf,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,UAAU;IACV,kBAAkB;IAClB,QAAQ;IACR,uBAAuB;AAC3B;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA,aAAa;;AAEb;IACI,aAAa;IACb,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,qBAAqB;IACrB,UAAU;IACV,iBAAiB;IACjB,wCAAwC;IACxC,aAAa;IACb,aAAa;AACjB;;AAEA;IACI,mBAAmB;IACnB,gCAAgC;AACpC;;AAEA,YAAY;;AAEZ;IACI,aAAa;IACb,kCAAkC;IAClC,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,UAAU;AACd","sourcesContent":[":root {\n    --softBlack: rgba(0,0,0,0.75);\n}\n\n* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nbody {\n    min-height: 100vh;\n    color: white;\n    display: grid;\n    grid-template-rows: 200px 1fr 250px;\n    background-color: black;\n    background-image: url(./assets/pagoda-background.jpg);\n    background-repeat: no-repeat;\n    font-size: 1.2em;\n}\n\n/* Classes */\n\n.button {\n    cursor: pointer;\n}\n\n.icon {\n    filter: invert(100%) sepia(0%) saturate(18%) hue-rotate(314deg) brightness(106%) contrast(104%);\n}\n\n.scaled {\n    scale: 2.7;\n}\n\n.title-font {\n    font-family: 'Urbanist', sans-serif;\n    font-weight: 300;\n    font-size: 1.7em;\n}\n\n/* Paragraphs */\n\n.txt-wrapper {\n    padding: 30px;\n    background-color: var(--softBlack);\n}\n\n/* Images */\n\n.img-w